@import url(https://fonts.googleapis.com/css2?family=Karla:wght@700&display=swap);
/* Mobile view */

.bio {
    font-size: 14px;
    line-height: 21px;
    margin: 1px 43px 0 43px;
    text-align: center;
}

.bio, .username {
    font-family: 'Karla', sans-serif;
}

.info {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 28px 0 30px 0;
}

.image {
    border-radius: 50%;
    height: 96px;
    margin-bottom: 7px;
    width: 96px;
}

.username {
    font-size: 16px;
}
/* Mobile view */

.hide {
    visibility: hidden;
}

.icon {
    -webkit-filter: invert(100%) sepia(37%) saturate(103%) hue-rotate(258deg) brightness(113%) contrast(300%);
            filter: invert(100%) sepia(37%) saturate(103%) hue-rotate(258deg) brightness(113%) contrast(300%);
    height: 32px;
    width: 32px;
}

.icon-alignment {
    display: flex;
    padding-left: 9px;
    width: 100%;
}

.link {
    align-items: center;
    color: white;
    display: grid;
    font-family: 'Karla', sans-serif;
    font-size: 16px;
    grid-template: 100% / 16% 68% 16%;
    height: 100%;
    justify-items: center;
    text-decoration: none;
}

.link-item {
    background-color: #4f7cfe;
    border: 2px solid #4f7cfe;
}

.link-item:hover {
    background-color: transparent;
}

.link-item:hover .icon {
    -webkit-filter: invert(39%) sepia(78%) saturate(1996%) hue-rotate(211deg) brightness(102%) contrast(99%);
            filter: invert(39%) sepia(78%) saturate(1996%) hue-rotate(211deg) brightness(102%) contrast(99%);
}

.link-item:hover .link {
    color: #4f7cfe;
}

.link-item, .social-item {
    margin: 0 4px 16px 4px;
    padding: 2px 0;
}

.links-grid {
    margin-top: 32px;
    padding-bottom: 80px;
    width: 100%;
}

.social-item {
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 16px 0;
}

.social-item a:hover {
    -webkit-transform: scale(1.075);
            transform: scale(1.075);
}

.svg {
    fill: #4f7cfe;
    height: 32px;
    width: 32px;
}
/* Mobile view */

.btn-svg {
    background: none;
    border: none;
    margin: 0;
    padding: 0;
    text-align: center;
}

.linktree-svg {
    height: 100%;
    width: 36.5%;
}

.footer {
    align-items: center;
    display: flex;
    justify-content: center;
    padding-bottom: 16px;
    width: 100%;
}
/*  */

.linktree {
    max-width: 680px;
    width: 100%;
}

.linktree, #root {
    margin: 0;
    padding: 0;
}

#root {
    align-items: center;
    display: flex;
    flex-direction: column;
}
